import React from 'react'
import styled from 'styled-components'
import { v4 as uuidv4  } from 'uuid'


const PostsForm = ({posts, changePosts, title, changeTitle, text, changeText, author, changeAuthor}) => {

        const handleOnSubmit = (e) => {
            e.preventDefault()
            if(title.length === 0 || text.length === 0 || author.length === 0){
                return;
            }
                    changePosts([...posts,
                        {
                            title: title,
                            text: text,
                            author: author,
                            date: Date(),
                            id: uuidv4()
                        }
                    ])
            changeTitle('')
            changeText('')
            changeAuthor('')
        }

        const handleOnChange = (e) => {
            switch(e.target.name){
                case 'title': 
                    changeTitle(e.target.value)
                    break;
                case 'text':
                    changeText(e.target.value)
                    break;
                case 'author':
                    changeAuthor(e.target.value)
                    break;
                default:
                    alert('Valor no encontrado')
            }
        }


    return(
        <div>
            <StyledForm action="" onSubmit={handleOnSubmit}>               
                <InputFormContainer>
                    <label htmlFor="title">Title: </label>
                    <StyledInputsForPosts 
                        type="text"
                        name="title" 
                        id="title"
                        value={title}                        
                        onChange={handleOnChange}
                        placeholder="Title"
                    />
                </InputFormContainer>
                <InputFormContainer>
                    <label htmlFor="text">Text: </label>
                    <StyledInputsForPosts 
                        type="text" 
                        name="text"
                        id="text"
                        value={text}
                        onChange={handleOnChange}
                        placeholder="Text"
                        text
                    />
                </InputFormContainer>
                <InputFormContainer>
                    <label htmlFor="author">Author: </label>
                    <StyledInputsForPosts 
                        type="text" 
                        name="author"
                        id="author"
                        value={author}
                        onChange={handleOnChange}
                        placeholder="Author"
                    />
                        <br />

                    <button className="submit__button" type="submit">                        
                        Upload Post
                    </button>
                </InputFormContainer>    
            </StyledForm>
        </div>
    )
}

const StyledForm = styled.form`
    display: flex;
    flex-direction: column;
    width: 50%;
    text-align: left;
    font-size: 28px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 900;
`
const InputFormContainer = styled.div`
    margin: 10px;
`
const StyledInputsForPosts = styled.input`
    outline: none;
    background-color: rgba(210, 255, 210,0.8);
    border: 0;
    border-bottom: 2px solid black;
    font-size: ${props => props.text ? '14px' : '18px'};
    width: 100%;
    height: 40px;
    margin: 0 0 20px 0;
    &::placeholder{
        font-size: 18px;
        padding-left: 5px;
    }
`
export default PostsForm